import { Component,Input,Output} from '@angular/core';
import { DownupBox } from '../app-directive/downup.animation.directive';
import { UnlessDirective } from '../app-directive/unless.directive';
import { XxjSwipeCountDirective } from '../app-directive/xxj-swipe-count.directive';

@Component({
    selector: 'downupAnimation',
	template: `
    <div downup-box #box="downup" style="height:0; width:50%; overflow: hidden;">Some content</div>
    <button (click)="box.toggle(visible = !visible)">Animate</button>
    <br><hr>
    <p *myUnless="condition">
       condition is false and myUnless is true.
    </p>
    <hr>
    <p *myUnless="!condition">
       condition is true and myUnless is false.
    </p>
    <hr>
    <button (click)="changeShow()">{{condition ? "show" :"hide"}}</button>
    <br><hr>
    <div swipe-count>Counting how many swipes</div>
	`,
    styles:[ `
            button {font-size:1.2em;width:6em;height:1.5em; background-color:grey;}
        ` ],
    directives : [DownupBox,UnlessDirective,XxjSwipeCountDirective]
})
export class DownupAnimationComponent {
  condition:boolean=true;
  visible: boolean = false;
  hideMyChild: Object = {};
  items: number[] = [1,2,3,4,5];
  changeShow(){
      this.condition=!this.condition;
  }
}
